import React from 'react';

export default function DashboardNavbar({ role }) {
  return (
    <nav style={{
      width: '100%',
      background: '#2e2c2cff',
      padding: '12px 24px',
      display: 'flex',
      justifyContent: 'space-between',
      alignItems: 'center',
      color: '#fff'
    }}>
      <div style={{ fontSize: '25px', fontWeight: '700' }}>
        Fralytics
      </div>
      
      <div style={{ display: 'flex', alignItems: 'center', gap: '20px' }}>
        <span style={{ fontSize: '15px' }}>Logged in as:</span>
        <span style={{
          fontWeight: '600',
          fontSize: '16px',
          background: 'rgba(255,255,255,0.2)',
          padding: '6px 12px',
          borderRadius: '12px'
        }}>
          {role}
        </span>
        <button style={{
          background: 'transparent',
          border: '1px solid #fff',
          color: '#fff',
          padding: '6px 12px',
          borderRadius: '8px',
          cursor: 'pointer'
        }}
        onClick={() => window.location.href="/login"}>
          Logout
        </button>
      </div>
    </nav>
  );
}
