// Navbar.js
import React from 'react'

export default function Navbar() {
  const handleClick = (e, targetId) => {
    e.preventDefault();
    const element = document.getElementById(targetId);
    if (element) {
      const navbarHeight = 80; // Approximate navbar height
      const elementPosition = element.getBoundingClientRect().top + window.pageYOffset;
      const offsetPosition = elementPosition - navbarHeight;
      
      window.scrollTo({
        top: offsetPosition,
        behavior: 'smooth'
      });
    }
  };

  return (
        <header className="d-flex flex-wrap justify-content-center py-3 border-bottom bg-dark" style={{position: 'sticky', top: 0, zIndex: 1000}}> 
            <a href="#home" onClick={(e) => handleClick(e, 'home')} className="d-flex align-items-center mb-3 mb-md-0 me-md-auto link-body-emphasis text-decoration-none"> 
            <span className="fs-3 mx-5 text-white">🌳 FRALYTICS</span> 
            </a> 
            <ul className="nav nav-pills"> 
                <li className="nav-item">
                    <a href="#home" onClick={(e) => handleClick(e, 'home')} className="nav-link text-white" aria-current="page">Home</a>
                </li> 
                <li className="nav-item">
                    <a href="#about" onClick={(e) => handleClick(e, 'about')} className="nav-link text-white">About</a>
                </li> 
                <li className="nav-item">
                    <a href="#features" onClick={(e) => handleClick(e, 'features')} className="nav-link text-white">Features</a>
                </li> 
                <li className="nav-item">
                    <a href="#contact" onClick={(e) => handleClick(e, 'contact')} className="nav-link text-white">Contact Us</a>
                </li> 
                <li className="nav-item">
                    <a href="#faqs" onClick={(e) => handleClick(e, 'faqs')} className="nav-link text-white">FAQs</a>
                </li>
            </ul> 
        </header> 
  )
}