import React, { useState } from 'react';

export default function ContactUs() {
  const [formData, setFormData] = useState({ name: '', email: '', message: '' });
  const [errors, setErrors] = useState({});
  const [submitted, setSubmitted] = useState(false);

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData(prev => ({ ...prev, [name]: value }));
    if (errors[name]) setErrors(prev => ({ ...prev, [name]: '' }));
  };

  const validateForm = () => {
    const newErrors = {};
    if (!formData.name.trim()) newErrors.name = 'Name is required';
    if (!formData.email.trim()) newErrors.email = 'Email is required';
    else if (!/\S+@\S+\.\S+/.test(formData.email)) newErrors.email = 'Email is invalid';
    if (!formData.message.trim()) newErrors.message = 'Message is required';
    return newErrors;
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    const newErrors = validateForm();
    if (Object.keys(newErrors).length > 0) return setErrors(newErrors);

    console.log('Form submitted:', formData);
    setSubmitted(true);
    setFormData({ name: '', email: '', message: '' });
    setTimeout(() => setSubmitted(false), 3000);
  };

  return (
    <>
    <div className="container my-5 bordered-container" style={{ maxWidth: '1140px' }}>
      <style>{`
        .contact-section h2 {
          font-weight: 600;
          color: #2c3e50;
        }
        
        .contact-form-wrapper {
          background: #ffffff;
          border-radius: 8px;
          padding: 30px;
          box-shadow: 0 2px 8px rgba(0,0,0,0.08);
        }
        
        .contact-form-wrapper .form-label {
          font-weight: 500;
          color: #495057;
          margin-bottom: 8px;
        }
        
        .contact-form-wrapper .form-control {
          border: 1px solid #dce0e4;
          border-radius: 6px;
          padding: 10px 14px;
          transition: border-color 0.2s ease;
        }
        
        .contact-form-wrapper .form-control:focus {
          border-color: #28a745;
          box-shadow: 0 0 0 0.2rem rgba(40, 167, 69, 0.15);
          outline: none;
        }
        
        .contact-form-wrapper .btn-primary {
          background-color: #28a745;
          border-color: #28a745;
          padding: 12px;
          font-weight: 500;
          transition: background-color 0.2s ease;
        }
        
        .contact-form-wrapper .btn-primary:hover {
          background-color: #218838;
          border-color: #1e7e34;
        }
        
        .map-wrapper {
          border-radius: 8px;
          overflow: hidden;
          box-shadow: 0 2px 8px rgba(0,0,0,0.08);
        }
        
        .map-wrapper iframe {
          display: block;
        }
        
        .alert-success {
          border-radius: 6px;
          border-left: 4px solid #28a745;
        }
        .bordered-container {
        border: 3px solid transparent;
        border-radius: 10px;
        padding: 20px;
        background: linear-gradient(white, white) padding-box,
                    linear-gradient(90deg, #28a745, #00ff88, #28a745) border-box;
        }

      `}</style>
      
      <div className="contact-section">
        <h2 className="text-center mb-4">Contact Us</h2>

        <div className="row">
          <div className="col-md-6">
            <div className="contact-form-wrapper">
              {submitted && (
                <div className="alert alert-success mb-3">
                  Your message has been sent successfully!
                </div>
              )}

              <div>
                <div className="mb-3">
                  <label className="form-label">Name *</label>
                  <input
                    type="text"
                    className={`form-control ${errors.name ? 'is-invalid' : ''}`}
                    name="name"
                    value={formData.name}
                    onChange={handleChange}
                    placeholder="Enter your name"
                  />
                  {errors.name && <div className="invalid-feedback d-block">{errors.name}</div>}
                </div>

                <div className="mb-3">
                  <label className="form-label">Email *</label>
                  <input
                    type="email"
                    className={`form-control ${errors.email ? 'is-invalid' : ''}`}
                    name="email"
                    value={formData.email}
                    onChange={handleChange}
                    placeholder="Enter your email"
                  />
                  {errors.email && <div className="invalid-feedback d-block">{errors.email}</div>}
                </div>

                <div className="mb-3">
                  <label className="form-label">Message *</label>
                  <textarea
                    className={`form-control ${errors.message ? 'is-invalid' : ''}`}
                    name="message"
                    rows="4"
                    value={formData.message}
                    onChange={handleChange}
                    placeholder="Type your message here..."
                  ></textarea>
                  {errors.message && <div className="invalid-feedback d-block">{errors.message}</div>}
                </div>

                <button type="button" onClick={handleSubmit} className="btn btn-primary w-100">
                  Send Message
                </button>
              </div>
            </div>
          </div>

          <div className="col-md-6 mt-4 mt-md-0">
            <div className="map-wrapper">
              <iframe
                src="https://www.google.com/maps/embed?pb=!1m12!1m8!1m3!1d3782.9446040507282!2d73.8660085!3d18.5314053!3m2!1i1024!2i768!4f13.1!2m1!1saissms%20ioit%20location!5e0!3m2!1sen!2sin!4v1759347671824!5m2!1sen!2sin"
                width="100%"
                height="450"
                style={{ border: 0 }}
                allowFullScreen
                loading="lazy"
                referrerPolicy="no-referrer-when-downgrade"
                title="aissms-ioit-map"
              />
            </div>
          </div>
        </div>
      </div>
    </div>

    </>
  );
}