import React from 'react';
import { useNavigate } from 'react-router-dom';

export default function Carousel() {
  const navigate = useNavigate();

  const handleExploreClick = (e) => {
    e.preventDefault();
    navigate('/login');
  };

  return (
    <div className="container my-0" style={{ height: "680px" }}>
      <div id="carouselExampleAutoplaying" className="carousel slide" data-bs-ride="carousel">
        <div className="carousel-inner">

          {/* Slide 1 */}
          <div className="carousel-item active position-relative overlay-hover">
            <img
              src={`${process.env.PUBLIC_URL}/Wallpapers-HD-Forest.jpg`}
              className="d-block w-100"
              alt="Slide 1"
              style={{ height: "680px", objectFit: "cover" }}
            />

            {/* Dark overlay */}
            <div className="overlay-dark position-absolute top-0 start-0 w-100 h-100"></div>

            {/* Quote & Button */}
            <div
              className="position-absolute top-50 start-50 translate-middle text-center"
              style={{ color: "#fff", zIndex: 2 }}
            >
              <h3 className="mb-3 carousel-quote">
                "Empowering communities, protecting our forests, and shaping a sustainable India — where data drives action and every citizen counts."
              </h3>
              <button 
                onClick={handleExploreClick} 
                className="btn btn-success btn-lg carousel-btn"
              >
                Explore Now
              </button>
            </div>
          </div>

          {/* Slide 2 */}
          <div className="carousel-item">
            <img
              src={`${process.env.PUBLIC_URL}/prime-minister-narendra-modi-with-his-cabinet-868576.jpg`}
              className="d-block w-100"
              alt="Slide 2"
              style={{ height: "680px", objectFit: "cover" }}
            />
          </div>

          {/* Slide 3 */}
          <div className="carousel-item">
            <img
              src={`${process.env.PUBLIC_URL}/forest-dwellers-reuters15219.jpg`}
              className="d-block w-100"
              alt="Slide 3"
              style={{ height: "680px", objectFit: "cover" }}
            />
          </div>

        </div>

        {/* Controls */}
        <button
          className="carousel-control-prev"
          type="button"
          data-bs-target="#carouselExampleAutoplaying"
          data-bs-slide="prev"
        >
          <span className="carousel-control-prev-icon" aria-hidden="true"></span>
          <span className="visually-hidden">Previous</span>
        </button>
        <button
          className="carousel-control-next"
          type="button"
          data-bs-target="#carouselExampleAutoplaying"
          data-bs-slide="next"
        >
          <span className="carousel-control-next-icon" aria-hidden="true"></span>
          <span className="visually-hidden">Next</span>
        </button>

      </div>

      <style>{`
        .overlay-dark {
          background-color: rgba(0,0,0,0); /* default no dim */
          transition: background-color 0.3s ease;
          pointer-events: none;
        }

        /* dim the background image on hover of the carousel-item */
        .overlay-hover:hover .overlay-dark {
          background-color: rgba(0,0,0,0.4);
        }

        .carousel-quote {
          transition: transform 0.3s, text-shadow 0.3s;
        }

        .carousel-quote:hover {
          transform: scale(1.05);
          text-shadow: 3px 3px 12px rgba(0,0,0,0.9);
        }

        .carousel-btn {
          transition: transform 0.3s, box-shadow 0.3s;
        }

        .carousel-btn:hover {
          transform: scale(1.05);
          box-shadow: 0 8px 20px rgba(0,0,0,0.4);
        }
      `}</style>
    </div>
  );
}