import React, { useState, useEffect } from 'react';
import { Link, useLocation } from 'react-router-dom';

export default function Sidebar({ userRole, userName }) {
  const [collapsed, setCollapsed] = useState(false);
  const [user, setUser] = useState({ name: 'Demo User', role: 'Visitor' });
  const location = useLocation();

  useEffect(() => {
    const storedName = localStorage.getItem('userName') || userName;
    const storedRole = localStorage.getItem('userRole') || userRole;
    setUser({ name: storedName || 'Demo User', role: storedRole || 'Visitor' });
    localStorage.setItem('userName', storedName || 'Demo User');
    localStorage.setItem('userRole', storedRole || 'Visitor');
  }, [userName, userRole]);

  const toggleSidebar = () => setCollapsed(!collapsed);
  const sidebarStyle = {
    width: collapsed ? '80px' : '280px',
    transition: 'width 0.3s',
    minHeight: '100vh',
    backgroundColor: '#1a1a1a',
    color: '#fff',
    overflow: 'hidden',
  };
  const iconOnly = collapsed ? { display: 'none' } : {};
  const iconSize = 24;
  const strokeWidth = 1.5;

  const menuItems = [
    {
      name: 'Dashboard',
      path: '/dashboard',
      icon: (
        <svg xmlns="http://www.w3.org/2000/svg" width={iconSize} height={iconSize} fill="none" viewBox="0 0 16 16" stroke="currentColor" strokeWidth={strokeWidth}>
          <rect x="1.5" y="1.5" width="5" height="5" />
          <rect x="9.5" y="1.5" width="5" height="5" />
          <rect x="1.5" y="9.5" width="5" height="5" />
          <rect x="9.5" y="9.5" width="5" height="5" />
        </svg>
      ),
    },
    {
      name: 'Digitization',
      path: '/digitization',
      icon: (
        <svg xmlns="http://www.w3.org/2000/svg" width={iconSize} height={iconSize} fill="none" viewBox="0 0 16 16" stroke="currentColor" strokeWidth={strokeWidth}>
          <rect x="3" y="1.5" width="10" height="13" rx="1"/>
          <line x1="4" y1="4" x2="12" y2="4"/>
          <line x1="4" y1="7" x2="12" y2="7"/>
          <line x1="4" y1="10" x2="12" y2="10"/>
        </svg>
      ),
    },
    {
      name: 'WebGIS Integration',
      path: '/webgis',
      icon: (
        <svg xmlns="http://www.w3.org/2000/svg" width={iconSize} height={iconSize} fill="none" viewBox="0 0 16 16" stroke="currentColor" strokeWidth={strokeWidth}>
          <polygon points="1,2 7,1 13,2 13,13 7,14 1,13"/>
          <line x1="7" y1="1" x2="7" y2="14"/>
          <line x1="1" y1="7" x2="13" y2="7"/>
        </svg>
      ),
    },
    {
      name: 'Decision Support System',
      path: '/dss',
      icon: (
        <svg xmlns="http://www.w3.org/2000/svg" width={iconSize} height={iconSize} fill="none" viewBox="0 0 16 16" stroke="currentColor" strokeWidth={strokeWidth}>
          <circle cx="8" cy="6" r="4"/>
          <line x1="4" y1="6" x2="12" y2="6"/>
          <line x1="4" y1="8" x2="12" y2="8"/>
          <line x1="4" y1="10" x2="12" y2="10"/>
        </svg>
      ),
    },
    {
      name: 'Online Claim Submission',
      path: '/claims',
      icon: (
        <svg xmlns="http://www.w3.org/2000/svg" width={iconSize} height={iconSize} fill="none" viewBox="0 0 16 16" stroke="currentColor" strokeWidth={strokeWidth}>
          <rect x="2" y="3" width="12" height="10" rx="1"/>
          <path d="M4.5 8.5l3 3 5-5"/>
        </svg>
      ),
    },
    {
      name: 'Settings',
      path: '/settings',
      icon: (
        <svg xmlns="http://www.w3.org/2000/svg" width={iconSize} height={iconSize} fill="none" viewBox="0 0 16 16" stroke="currentColor" strokeWidth={strokeWidth}>
          <circle cx="8" cy="8" r="7"/>
          <circle cx="8" cy="8" r="3"/>
        </svg>
      ),
    },
  ];

  return (
    <div style={sidebarStyle} className="d-flex flex-column p-3">
      {/* Hamburger toggle */}
      <div className="d-flex align-items-center mb-3 justify-content-center">
        <button className="btn btn-sm btn-outline-light" onClick={toggleSidebar}>
          <svg xmlns="http://www.w3.org/2000/svg" width={iconSize} height={iconSize} fill="none" viewBox="0 0 16 16" stroke="currentColor" strokeWidth={strokeWidth} strokeLinecap="round">
            <path d="M2 4h12M2 8h12M2 12h12"/>
          </svg>
        </button>
      </div>
      <hr />

      {/* Navigation */}
      <ul className="nav nav-pills flex-column mb-auto">
        {menuItems.map((item) => (
          <li key={item.name} className="nav-item mb-1">
            <Link
              to={item.path}
              className={`nav-link text-white d-flex align-items-center ${location.pathname === item.path ? 'active bg-secondary' : ''}`}
            >
              {item.icon}
              <span style={iconOnly} className="ms-2">{item.name}</span>
            </Link>
          </li>
        ))}
      </ul>
      <hr />

      {/* User info */}
      <div className="dropdown mt-auto">
        <a
          href="/"
          className="d-flex align-items-center text-white text-decoration-none dropdown-toggle"
          data-bs-toggle="dropdown"
          aria-expanded="false"
        >
          <img
            src="https://github.com/mdo.png"
            alt=""
            width="32"
            height="32"
            className="rounded-circle me-2"
          />
          <div style={iconOnly}>
            <strong>{user.name}</strong>
            <div style={{ fontSize: '12px', color: '#ccc' }}>{user.role}</div>
          </div>
        </a>
        <ul className="dropdown-menu dropdown-menu-dark text-small shadow">
          <li><Link className="dropdown-item" to="/settings">Settings</Link></li>
          <li><Link className="dropdown-item" to="/profile">Profile</Link></li>
          <li><hr className="dropdown-divider" /></li>
          <li><Link className="dropdown-item" to="/logout">Sign out</Link></li>
        </ul>
      </div>
    </div>
  );
}
