import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';

export default function Login() {
  const navigate = useNavigate();
  const [isSignUp, setIsSignUp] = useState(false);
  const [demoRole, setDemoRole] = useState("Ministry Official"); // default role

  // Navigate to dashboard with selected role
  const handleDemo = (e) => {
    e.preventDefault();
    navigate('/dashboard', { state: { role: demoRole } });
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    // Normally, you would validate login here
    navigate('/dashboard', { state: { role: "Ministry Official" } }); // default login role
  };

  return (
    <div style={{
      minHeight: '100vh',
      background: 'linear-gradient(135deg, #2d5016 0%, #1a3409 100%)',
      display: 'flex',
      alignItems: 'center',
      justifyContent: 'center',
      padding: '20px',
      position: 'relative',
      overflow: 'hidden'
    }}>
      {/* Background forest overlay */}
      <div style={{
        position: 'absolute',
        inset: 0,
        backgroundImage: 'url(https://images.unsplash.com/photo-1511497584788-876760111969?w=1920)',
        backgroundSize: 'cover',
        backgroundPosition: 'center',
        opacity: '0.2',
        zIndex: 0
      }}></div>

      {/* Main container */}
      <div style={{
        background: 'rgba(255, 255, 255, 0.1)',
        backdropFilter: 'blur(20px)',
        borderRadius: '24px',
        boxShadow: '0 20px 50px rgba(0, 0, 0, 0.3)',
        maxWidth: '1000px',
        width: '100%',
        position: 'relative',
        zIndex: 1,
        border: '1px solid rgba(255, 255, 255, 0.2)',
        overflow: 'hidden',
        display: 'flex',
        minHeight: '470px'
      }}>

        {/* Left side - Branding */}
        <div style={{
          flex: '1',
          background: 'linear-gradient(135deg, rgba(45, 80, 22, 0.9) 0%, rgba(26, 52, 9, 0.9) 100%)',
          padding: '35px 25px',
          display: 'flex',
          flexDirection: 'column',
          justifyContent: 'center',
          alignItems: 'center',
          position: 'relative',
          overflow: 'hidden'
        }}>
          <div style={{
            position: 'absolute',
            inset: 0,
            backgroundImage: 'url(https://images.unsplash.com/photo-1441974231531-c6227db76b6e?w=800)',
            backgroundSize: 'cover',
            backgroundPosition: 'center',
            opacity: '0.3',
            zIndex: 0
          }}></div>

          <div style={{ position: 'relative', zIndex: 1, textAlign: 'center' }}>
            <h1 style={{ fontSize: '38px', fontWeight: '800', color: '#fff', marginBottom: '18px' }}>
              Fralytics
            </h1>
            <p style={{ color: '#e0e0e0', fontSize: '17px', lineHeight: '1.6', maxWidth: '300px' }}>
              An eco-friendly analytics platform to monitor the implementation of Forest Right Act, 2006.
            </p>
          </div>
        </div>

        {/* Right side - Form */}
        <div style={{
          flex: '1',
          background: 'rgba(255, 255, 255, 0.98)',
          padding: '35px 25px',
          display: 'flex',
          flexDirection: 'column',
          justifyContent: 'center'
        }}>

          {/* Form header */}
          <div style={{ marginBottom: '28px' }}>
            <h2 style={{ fontSize: '26px', fontWeight: '700', color: '#2d5016', marginBottom: '6px' }}>
              {isSignUp ? 'Create Account' : 'Welcome Back'}
            </h2>
            <p style={{ color: '#666', fontSize: '15px', margin: 0 }}>
              {isSignUp ? 'Join us in protecting our forests' : 'Sign in to continue'}
            </p>
          </div>

          <form onSubmit={handleSubmit}>
            {isSignUp && (
              <div style={{ marginBottom: '14px' }}>
                <label style={{ display: 'block', marginBottom: '6px', color: '#333', fontSize: '15px', fontWeight: '500' }}>
                  Full Name
                </label>
                <input type="text" placeholder="Enter your full name" required
                  style={{ padding: '11px 13px', borderRadius: '10px', border: '2px solid #e0e0e0', width: '100%', fontSize: '15px' }}
                />
              </div>
            )}

            <div style={{ marginBottom: '14px' }}>
              <label style={{ display: 'block', marginBottom: '6px', color: '#333', fontSize: '15px', fontWeight: '500' }}>
                Email Address
              </label>
              <input type="email" placeholder="name@example.com" required
                style={{ padding: '11px 13px', borderRadius: '10px', border: '2px solid #e0e0e0', width: '100%', fontSize: '15px' }}
              />
            </div>

            <div style={{ marginBottom: '14px' }}>
              <label style={{ display: 'block', marginBottom: '6px', color: '#333', fontSize: '15px', fontWeight: '500' }}>
                Password
              </label>
              <input type="password" placeholder="Enter your password" required
                style={{ padding: '11px 13px', borderRadius: '10px', border: '2px solid #e0e0e0', width: '100%', fontSize: '15px' }}
              />
            </div>

            {!isSignUp && (
              <div className="form-check" style={{ marginBottom: '18px' }}>
                <input className="form-check-input" type="checkbox" id="rememberMe" />
                <label htmlFor="rememberMe" style={{ color: '#666', fontSize: '15px' }}>
                  Remember me
                </label>
              </div>
            )}

            <button type="submit"
              style={{
                width: '100%', padding: '11px',
                background: 'linear-gradient(135deg, #4caf50 0%, #2d5016 100%)',
                color: 'white', border: 'none', borderRadius: '10px',
                fontSize: '16px', fontWeight: '600', marginBottom: '12px'
              }}>
              {isSignUp ? 'Create Account' : 'Sign In'}
            </button>

            <div style={{ marginBottom: '12px' }}>
              <label style={{ fontSize: '15px', fontWeight: '500', color: '#333' }}>
                View as (Demo Role)
              </label>
              <select
                value={demoRole}
                onChange={(e) => setDemoRole(e.target.value)}
                style={{
                  width: '100%', padding: '9px',
                  borderRadius: '10px', border: '2px solid #4caf50',
                  fontSize: '15px', marginTop: '5px'
                }}
              >
                <option>Ministry Official</option>
                <option>State Official</option>
                <option>District Official</option>
                <option>Claimant</option>
              </select>
            </div>

            <button onClick={handleDemo} type="button"
              style={{
                width: '100%', padding: '11px',
                background: 'transparent', color: '#4caf50',
                border: '2px solid #4caf50', borderRadius: '10px',
                fontSize: '16px', fontWeight: '600'
              }}>
              Try as Demo User
            </button>

            <div style={{ textAlign: 'center', paddingTop: '14px', borderTop: '1px solid #e0e0e0' }}>
              <p style={{ color: '#666', fontSize: '15px' }}>
                {isSignUp ? 'Already have an account?' : "Don't have an account?"}
              </p>
              <button type="button" onClick={() => setIsSignUp(!isSignUp)}
                style={{ background: 'none', border: 'none', color: '#4caf50', fontSize: '16px', fontWeight: '600', textDecoration: 'underline' }}>
                {isSignUp ? 'Sign In' : 'Create Account'}
              </button>
            </div>
          </form>

          <p style={{ textAlign: 'center', color: '#999', fontSize: '14px', marginTop: '18px' }}>
            © 2025 FRALYTICS. All rights reserved.
          </p>
        </div>
      </div>
    </div>
  );
}
